import React from 'react';
import { NavLink } from 'react-router-dom';
import img1 from "../images/waleedcodes-logo.png"

const Footer = () => {
    return (
        <>
            {/* ************************** */}
            <div className="section9">
                <div className="container sec9_main">
                    <div className="row gx-0">
                        <h2 className=''>Get our latest offer</h2>
                        <h5 className=''>by subscription</h5>
                        <div className="col-lg-3"></div>
                        <div className="col-lg-6 text-center">
                            <form action="">
                                <div class="input-group pt-4">
                                    <i class="fa-solid fa-envelope input-group-text" id="basic-addon1"></i>
                                    <input type="text" class="form-control" placeholder="EMAIL ADDRESS" aria-label="Username" aria-describedby="basic-addon1" required />
                                    <button type='submit' id='btn1' className='py-3 px-4 btn2 border-0'>Get the Coupon</button>
                                </div>
                                <div className='text-center pt-4 sec9_last'>
                                    <button type='submit' id='btn2' className='py-3 px-4 btn2 border-0'>Get the Coupon</button>
                                </div>
                            </form>
                        </div>
                        <div className="col-lg-3"></div>
                    </div>
                </div>
            </div>
            {/* ************************** */}
            <div className="footer">
                <div className="container footer_main">
                    <div className="row text-center">
                        <div className="col-md-12">
                            <NavLink className="nav_img" to="/">
                                <img src={img1} alt="" className='img-fluid' />
                            </NavLink>
                        </div>
                    </div>
                    <div className="row gx-0 ft_row2">
                        <div className="col-lg-4">
                            <h5>About Us</h5>
                            <p>We know there are a lot of threa developers our but we pride into a firm in the industry.</p>
                        </div>
                        <div className="col-lg-3">
                            <h5>Feature</h5>
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ft_nav">
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/about"> About Us</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/products">Terms Condition
                                    </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/blog">   Best Products</NavLink>
                                </li>
                            </ul>
                        </div>
                        <div className="col-lg-3">
                            <h5>General Links</h5>
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ft_nav">
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/about"> Blog</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/products">
                                        Shop</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/blog">   Support</NavLink>
                                </li>
                            </ul>
                        </div>
                        <div className="col-lg-2">
                            <h5>Helpful</h5>
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ft_nav">
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/about"> About Us</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/products">
                                        Popular Category</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/blog">   Contact Us</NavLink>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="row text-center">
                        <p>2023 Waleedcodes All rights reserved</p>
                    </div>
                </div>
            </div>
            {/* ************************** */}
        </>
    )
}

export default Footer