import React from 'react';
import "../App.css"
import { useState, useEffect } from 'react';
import img1 from "../images/playstore.png"
import img2 from "../images/appstore.png"

const Timer = () => {
    const [days, setDays] = useState(0);
    const [hours, setHours] = useState(0);
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);

    const deadline = "December, 31, 2023";

    const getTime = () => {
        const time = Date.parse(deadline) - Date.now();

        setDays(Math.floor(time / (1000 * 60 * 60 * 24)));
        setHours(Math.floor((time / (1000 * 60 * 60)) % 24));
        setMinutes(Math.floor((time / 1000 / 60) % 60));
        setSeconds(Math.floor((time / 1000) % 60));
    };

    useEffect(() => {
        const interval = setInterval(() => getTime(deadline), 1000);

        return () => clearInterval(interval);
    }, []);

    return (
        <div className="timer_sec">

            <div className="container timer_main">
                <div className="row gx-0">
                    <div className="col-lg-6">
                        <div className="timer animate__animated animate__fadeInLeft" role="timer" >
                            <div className="row gx-0">
                                <div className="col-lg-3 col-6">
                                    <div className="timer_box1">
                                        <p id="day">{days < 10 ? "0" + days : days}</p>
                                        <span className="text">Days</span>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6">
                                    <div className="timer_box2">
                                        <p id="hour">{hours < 10 ? "0" + hours : hours}</p>
                                        <span className="text">Hours</span>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6">
                                    <div className="timer_box3">
                                        <p id="minute">{minutes < 10 ? "0" + minutes : minutes}</p>
                                        <span className="text">Minutes</span>
                                    </div>
                                </div>
                                <div className="col-lg-3 col-6">
                                    <div className="timer_box4">
                                        <p id="second">{seconds < 10 ? "0" + seconds : seconds}</p>
                                        <span className="text">Seconds</span>
                                    </div>
                                </div>
                            </div>
                            <h1 className=''>WOO! Flash Sale</h1>
                            <button className='btn2 border-0 mt-3'>SHOP NOW</button>
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="timer1 animate__animated animate__fadeInRight" >
                            <p>MOBILE APP VERSION</p>
                            <h2>Get Our <span> Mobile App </span></h2>
                            <h2>It's Make easy for you life!</h2>
                            <div className="d-lg-flex d-none mt-lg-5 text-center">
                                <img src={img1} alt="" className='mx-3 my-2 img-fluid' />
                                <img src={img2} alt="" className='mx-3 my-2 img-fluid' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Timer;