import React from 'react'
import img1 from "../images/waleedcodes-logo.png"
import { NavLink } from 'react-router-dom'

const Navbar = () => {
    return (
        <>

            <div className="container-fluid nav_sec0">
                <div className="container gx-0">
                    <div className="row align-items-center">
                        <div className="col-md-3">
                            <NavLink className="nav_img" to="/">
                                <img src={img1} alt="" className='img-fluid' />
                            </NavLink>
                        </div>
                        <div className="col-md-1"></div>
                        <div className="col-md-8">
                            <form class="input-group" role="search">
                                <input type="search" class="form-control" placeholder="Search products..." aria-label="search" />
                                <button class="btn1 border-0 px-5" type="button" >Search</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            {/*********************************************************** */}
            <nav className="navbar navbar-expand-lg nav_sec1">
                <div className="container gx-0">
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0 nav_ul">
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/">Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/about">About</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/products">Product
                                </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/blog">Blog</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/contact">Contact</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/seller">Seller</NavLink>
                            </li>
                            <div className="dropdown">
                                <div className="">
                                    <NavLink className="nav-link dropbtn" to="/contact">Pages  <i class="fa-solid fa-angle-down"></i></NavLink>
                                </div>
                                <div className="dropdown-content">
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/contact">Page1</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/contact">Page1</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link" to="/contact">Page1</NavLink>
                                    </li>
                                </div>
                            </div>
                        </ul>
                    </div>
                    <form className="cart1 d-flex align-items-end">
                        <button className="btn1 mx-3 border-0" type="button">Log In</button>
                        <NavLink to="/cart">
                            <i className="fa-solid fa-cart-shopping"></i>
                            <span className='cart-total--item'>50</span>
                        </NavLink>
                    </form>
                </div>
            </nav >
            {/* Navbar for mobiles OR tablet */}
            <nav nav className="navbar bg-body-tertiary nav_sec" >
                <div className="container text-center">
                    <button className="nav_btn1" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar" aria-label="Toggle navigation">
                        {/* <span className="navbar-toggler-icon"></span> */}
                        <i className="fa-solid fa-bars-staggered"></i>
                    </button>
                    <NavLink className="nav_img" to="/">
                        <img src={img1} alt="" className='img-fluid' />
                    </NavLink>
                    <form className="nav_mobile1">
                        {/* <button className="btn1 border-0 text-light" type="button">Log In</button> */}
                        <NavLink to="/cart" className='position-relative'>
                            <i className="fa-solid fa-cart-shopping"></i>
                            {/* <span className='cart-total--item'>50</span> */}
                            <span class="position-absolute top-20 start-100 translate-middle p-1 border border-light rounded-circle">
                                0
                            </span>
                        </NavLink>
                        {/* <button type="button" class="btn btn-primary position-relative">
                            Profile
                            <span class="position-absolute top-0 start-100 translate-middle p-2 bg-danger border border-light rounded-circle">
                                <span class="visually-hidden">New alerts</span>
                            </span>
                        </button> */}
                    </form>
                    <div className="offcanvas offcanvas-end" tabindex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel">
                        <div className="offcanvas-header">
                            <h5 className="offcanvas-title" id="offcanvasNavbarLabel">Welcome</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                        </div>
                        <div className="offcanvas-body bg-dark">
                            <ul className="navbar-nav justify-content-end flex-grow-1 pe-3 nav_ul">
                                {/* <li className="nav-item">
                                    <a className="nav-link" href="/">Home</a>
                                </li> */}
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/about">About</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/products">Products</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/blog">Blog</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/contact">Contact</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" to="/seller">Seller</NavLink>
                                </li>
                            </ul>
                            <form className="nav_mobile1 my-3">
                                <button className="btn1 border-0 text-light" type="button">Log In</button>
                            </form>
                        </div>
                    </div>
                </div>
            </nav >
        </>
    )
}

export default Navbar