import React from 'react';
import { NavLink } from 'react-router-dom';

const Product = (curElem) => {
    const { _id, name, img, price, category, rating } = curElem;
    return (
        <>
            <div className="container-fluid gx-0">
                <div className="row">
                    <NavLink className="text-decoration-none" to={`/singleproduct/${_id}`}>


                        <div className="col-md-12">
                            <div className="sec4_col1">
                                <span class="position-absolute top-0 px-4 py-2 badge rounded-5 headspan">
                                    {category}
                                </span>
                                <div class="card2 border-0 position-relative">
                                    <div className="sec4_icons">
                                        <button className=' animate__animated animate__fadeInRight animate__faster border-0 mt-3'><i class="fa-solid fa-maximize"></i>
                                        </button>
                                        <button className=' animate__animated animate__fadeInRight animate__faster border-0 mt-3'><i class="fa-sharp fa-regular fa-heart"></i></button>

                                        <button className=' animate__animated animate__fadeInRight animate__faster border-0 mt-3'><i class="fa-sharp fa-solid fa-arrows-rotate"></i></button>
                                    </div>
                                    <div className="product_img text-center">
                                        <img src={img} class="card-img-top" alt='' />
                                    </div>
                                    <div class="card-body2">
                                        <div className="py-3 sec4_star">
                                            <span class="fa fa-star checked"></span>
                                            {rating}
                                        </div>
                                        <h5 class="card-title">{name}</h5>
                                        <h3 class="card-text">${price}</h3>
                                    </div>
                                    <div className="cart_btn">
                                        <button className=' animate__animated animate__fadeInUp btn4 border-0 mt-3'><i class="fa-solid fa-bag-shopping"></i> Add To Cart</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </NavLink>

                </div>
            </div>

        </>
    )
}

export default Product